<template>
  <div class="tabbar">
    <div class="wrapper" ref="wrapper" v-bind:style="wrapStyle">
      <div class="bscroll-container">
        <!--  -->
        <!-- <div class="top-tip">
          <span class="refresh-hook">{{pulldownMsg}}</span>
        </div>-->
        <v-layout justify-center v-if="downProgress">
          <v-progress-circular indeterminate color="red"></v-progress-circular>
        </v-layout>
        <!--  -->
        <slot></slot>
        <!--  -->
        <v-layout justify-center v-if="hasMore">
          <v-btn flat></v-btn>
        </v-layout>
        <v-layout justify-center v-if="BottomLoad">
          <v-progress-circular indeterminate color="red"></v-progress-circular>
        </v-layout>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import BScroll from "better-scroll";

export default {
  name: "scroll",
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 1
    },
    /**
     * click
     */
    click: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    scrollX: {
      type: Boolean,
      default: true
    },
    /**
     * 
     * 
     */
    listenScroll: {
      type: Boolean,
      default: true
    },
    /**
     * item
     */
    item: {
      type: Object,
      default: null
    },
    /**
     * 
     */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    pulldown: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
     * scroll
     */
    refreshDelay: {
      type: Number,
      default: 20
    },
    hasMore: {
      type: Boolean,
      default: false
    },
    BottomLoad: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pullupMsg: "more",
      pulldownMsg: "",
      downProgress: false,
      wrapStyle: {
        width: "100%",
        height: (document.documentElement.clientHeight) * 0.8+'px',
        overflow: "hidden",
        position: "relative"
      }
    };
  },
  mounted() {
    // DOMbetter-scroll
    setTimeout(() => {
      this._initScroll();
    }, 20);
  },
  methods: {
    _initScroll() {
      if (!this.$refs.wrapper) {
        return;
      }
      // better-scroll
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX,
        mouseWheel: true
      });

      // 
      if (this.listenScroll) {
        this.scroll.on("scroll", pos => {
          if (pos.y > 50) {
            this.downProgress = true;
            this.pulldownMsg = "";
          }
          this.$emit("scroll", pos);
        });
      }

      // 
      if (this.pullup) {
        this.scroll.on("scrollEnd", () => {
          this.downProgress = false;
          // 
          if (this.scroll.y <= this.scroll.maxScrollY + 50) {
            this.$emit("scrollToEnd");
          }
        });
      }

      // 
      if (this.pulldown) {
        this.scroll.on("touchEnd", pos => {
          let y = Math.abs(this.scroll.distY);
          let x = this.scroll.distX;
          if (x > 80 && y < 25) {
            this.$emit("slideToRight");
          }

          this.pulldownMsg = "";
          // 
          if (pos.y > 50) {
            this.$emit("pulldown");
          }
        });
      }

      // 
      if (this.beforeScroll) {
        this.scroll.on("beforeScrollStart", () => {
          this.$emit("beforeScroll");
        });
      }
    },
    disable() {
      // better-scrolldisable
      this.scroll && this.scroll.disable();
    },
    enable() {
      // better-scrollenable
      this.scroll && this.scroll.enable();
    },
    refresh() {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh();
    },
    scrollTo() {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
    scrollToElement() {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
    }
  },
  watch: {
    // refreshDelayrefresh
    item() {
      setTimeout(() => {
        this.scroll.refresh();
      }, this.refreshDelay);
    }
  }
};
</script>
<style scoped>
.wrapper-father {
  position: fixed;
  top: 100px;
  bottom: 0;
}
.scroll-content {
  height: 95%;
}
/* .wrapper {
  width: 100%;
  height: 550px;
  overflow: hidden;
  position: relative;
} */

li {
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  text-align: center;
}

/*  */
.top-tip {
  position: absolute;
  top: -40px;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #555;
}

.bottom-tip {
  width: 100%;
  height: 35px;
  line-height: 35px;
  text-align: center;
  color: #777;
  background: #f2f2f2;
  position: absolute;
  bottom: -35px;
  left: 0;
}
</style>




